'use strict';

// MODULES //

import numelDimension from '@stdlib/ndarray-base-numel-dimension';
import getStride from '@stdlib/ndarray-base-stride';
import getData from '@stdlib/ndarray-base-data-buffer';
import getOffset from '@stdlib/ndarray-base-offset';
import { ndarray as strided } from '@stdlib/stats-strided-nanrange-by';


// MAIN //

/**
* Computes the range of a one-dimensional ndarray via a callback function, ignoring NaN values.
*
* @param {ArrayLikeObject<Object>} arrays - array-like object containing a one-dimensional input ndarray
* @param {Function} clbk - callback function
* @param {*} [thisArg] - callback execution context
* @returns {number} range
*
* @example
* import ndarray from '@stdlib/ndarray-base-ctor';
* import nanrangeBy from '@stdlib/stats-base-ndarray-nanrange-by';
*
* var xbuf = [ 1.0, -2.0, NaN, 2.0 ];
* var x = new ndarray( 'generic', xbuf, [ 4 ], [ 1 ], 0, 'row-major' );
*
* function clbk( value ) {
*     return value * 2.0;
* }
*
* var v = nanrangeBy( [ x ], clbk );
* // returns 8.0
*/
function nanrangeBy( arrays, clbk, thisArg ) {
	var x = arrays[ 0 ];
	return strided( numelDimension( x, 0 ), getData( x ), getStride( x, 0 ), getOffset( x ), wrapper, null ); // eslint-disable-line max-len

	/**
	* Invokes the provided callback.
	*
	* @private
	* @param {*} value - current array element
	* @param {NonNegativeInteger} aidx - current array element index
	* @param {NonNegativeInteger} sidx - current strided array element index
	* @param {Collection} arr - input array
	* @returns {*} result
	*/
	function wrapper( value, aidx ) {
		return clbk.call( thisArg, value, aidx, x );
	}
}


// EXPORTS //

export default nanrangeBy;
